const lstore = window.localStorage;

const Store = {

  // 新增备忘录
  addBook(name) {
    const bookID = this.createBookID();
    const books = this.getBooks();
    books.push({
      bookID,
      bookName: name,
      items: [],
      createTime: +new Date()
    });
    this.set('Books', books);
  },

  // 删除备忘录

  removeBook(bookID) {
    const books = this.getBooks();
    const index = books.findIndex(it => it.bookID === bookID);
    if (index > -1) {
      books.splice(index, 1);
    }
    this.set('Books', books);
  },

  // 新增备忘录子项
  addItem(bookID, content) {
    const books = this.getBooks();
    const index = books.findIndex(it => it.bookID === bookID);
    const match = books.find(it => it.bookID === bookID);
    /**
        *    *  itemID: item_456789,
       *        content: 'xx21w12',
       *        status:   0, 1, 2         0新增   1未完成  2完成
       *        reason: '未完成'          // 原因
       *        createTime: ''
     */
    const newID = this.createItemID();
    if (match) {
      match.items.push({
        itemID: newID,
        content,
        status: 0,
        reason: '',
        createTime: +new Date()
      });
      books[index] = match;
    }
    this.set('Books', books);
  },

  //  删除备忘录子项
  removeItem(bookID, itemID) {
    const books = this.getBooks();
    const bookIndex = books.findIndex(it => it.bookID === bookID);
    const itemIndex = books[bookIndex].items.findIndex(it => it.itemID === itemID);
    books[bookIndex].items.splice(itemIndex, 1);
    this.set('Books', books);
  },

  // 重命名备忘录
  reNameBook(bookID, name) {
    const books = this.getBooks();
    const bookIndex = books.findIndex(it => it.bookID === bookID);
    books[bookIndex].bookName = name;
    this.set('Books', books);
  },

  // 修改备忘录子项内容
  editItem(bookID, itemID, content) {
    const books = this.getBooks();
    const bookIndex = books.findIndex(it => it.bookID === bookID);
    const itemIndex = books[bookIndex].items.findIndex(it => it.itemID === itemID);
    books[bookIndex].items[itemIndex].content = content;
    this.set('Books', books);
  },

  // 生成bookID
  createBookID() {
    const newID = parseInt(Math.random() * 1000000000);
    return `book_${newID}`;
  },

  // 生成子项ID
  createItemID() {
    const newID = parseInt(Math.random() * 1000000000);
    return `item_${newID}`;
  },
  // 刷新
  refresh() {
    return this.get('Books');
  },

  getBooks() {
    return this.get('Books') || [];
  },

  // 标记完成
  markFinish(bookID, itemID) {
    const books = this.getBooks();
    const bookIndex = books.findIndex(it => it.bookID === bookID);
    const itemIndex = books[bookIndex].items.findIndex(it => it.itemID === itemID);
    books[bookIndex].items[itemIndex].status = 2;
    books[bookIndex].items[itemIndex].reason = '';
    books[bookIndex].items[itemIndex].finishTime = +new Date();
    this.set('Books', books);
  },
  // 标记未完成
  markUnFinish(bookID, itemID, reason) {
    const books = this.getBooks();
    const bookIndex = books.findIndex(it => it.bookID === bookID);
    const itemIndex = books[bookIndex].items.findIndex(it => it.itemID === itemID);
    books[bookIndex].items[itemIndex].status = 1;
    books[bookIndex].items[itemIndex].reason = reason;
    this.set('Books', books);
  },

  // 修改备注
  addReason(bookID, itemID, reason) {
    const books = this.getBooks();
    const bookIndex = books.findIndex(it => it.bookID === bookID);
    const itemIndex = books[bookIndex].items.findIndex(it => it.itemID === itemID);
    books[bookIndex].items[itemIndex].reason = reason;
    this.set('Books', books);
  },

  set(key, value) {
    lstore.setItem(key, JSON.stringify(value));
  },

  get(key) {
    return JSON.parse(lstore.getItem(key));
  }

  /** {
   *    bookID: book_123,
   *    bookName: '备忘1'，
   *    items: [
   *      {
   *        itemID: item_456789,
   *        content: 'xx21w12',
   *        status:   0, 1, 2         0新增   1未完成  2完成
   *        reason: '未完成'          // 原因
   *        createTime: ''
   *      }
   *    ],
   *    createTime
   *
   *  }
   ** */

};


export default Store;



