import React, {Component} from 'react';
import './book.less';
import Store from '../utils/store';
import classnames from 'classnames';

class Book extends Component {
  state = {
    books: [],
    activeBook: null,
    activeItem: null
  };

  componentDidMount() {
    this.refresh();
  }

  refresh() {
    // 更新books、activeBook
    const books = Store.getBooks();
    let matchActiveBook = null;
    let matchActiveItem = null;
    if (this.state.activeBook) {
      matchActiveBook = books.find(it => it.bookID === this.state.activeBook.bookID);
    }
    if (this.state.activeItem) {
      matchActiveItem = matchActiveBook.items.find(it => it.itemID === this.state.activeItem.itemID);
    }
    this.setState({
      books,
      activeBook: matchActiveBook,
      activeItem: matchActiveItem
    });
  }

  // 新增备忘
  addBook = () => {
    const name = window.prompt('请输入备忘录名称', '');
    if (name) {
      Store.addBook(name);
      this.refresh();
    }
  }

  // 删除备忘
  deleteBook = (item) => {
    Store.removeBook(item.bookID);
    this.setState({
      activeBook: null,
      activeItem: null
    }, () => {
      this.refresh();
    });
  }

  // 选中某个备忘录
  selectBook = (item) => {
    this.setState({
      activeBook: item,
      activeItem: null
    });
  }

  // 选中子分录
  selectItem = (item) => {
    this.setState({
      activeItem: item
    });
  }

  markFinish = () => {
    if (!this.state.activeItem) {
      window.alert('请选择要操作的子分录');
      return;
    }
    Store.markFinish(this.state.activeBook.bookID, this.state.activeItem.itemID);
    this.refresh();
  }

  markUnFinish = () => {
    if (!this.state.activeItem) {
      window.alert('请选择要操作的子分录');
      return;
    }
    const reason = window.prompt('请输入未完成原因', '');
    if (reason) {
      Store.markUnFinish(this.state.activeBook.bookID, this.state.activeItem.itemID, reason);
      this.refresh();
    }
  }

  // 新增子分录
  addItem = () => {
    if (!this.state.activeBook) {
      window.alert('请选择要操作的备忘录');
      return;
    }
    const content = window.prompt('请输入子分录内容', '');
    if (content) {
      Store.addItem(this.state.activeBook.bookID, content);
      this.refresh();
    }
  }

  deleteItem(item) {
    Store.removeItem(this.state.activeBook.bookID, item.itemID);
    this.refresh();
  }

  showTime(time) {
    const showTime = new Date(time);
    return `${showTime.getFullYear()}/${this.beTwo((showTime.getMonth() + 1))}/${this.beTwo(showTime.getDate())}  ${this.beTwo(showTime.getHours())}:${this.beTwo(showTime.getMinutes())}:${this.beTwo(showTime.getSeconds())}`;
  }

  beTwo(num) {
    if (num < 10) return `0${num}`;
    return num;
  }

  // 修改备注
  addReason = () => {
    if (!this.state.activeItem) {
      window.alert('请选择要操作的子分录');
      return;
    }
    const content = window.prompt('请输入备注内容', '');
    if (content) {
      Store.addReason(this.state.activeBook.bookID, this.state.activeItem.itemID, content);
      this.refresh();
    }
  }

  render() {
    return (<div className="book">
      <div className="top-part">
        在线备忘录 ©copyRight by wgh
      </div>
      <div className="main">
        <div className="left-part">
          <div className="title" onClick={this.addBook}>+新增备忘录</div>
          <div className="book-list">
            {
              this.state.books.map((item, index) => {
                const bookItemClass = classnames({
                  'book-item': true,
                  'active-book': this.state.activeBook ? item.bookID === this.state.activeBook.bookID : false
                });
                return (
                  <div key={index} className={bookItemClass} onClick={() => this.selectBook(item)}>
                    <span className="name">{item.bookName}</span>
                    <span className="close" onClick={() => this.deleteBook(item)}>x</span>
                  </div>
                );
              })
            }
          </div>
        </div>
        <div className="right-part">
          <div className="title">
            <span onClick={this.addItem}>+新增子分录</span>
            <span onClick={this.markFinish}>√标记为完成</span>
            <span onClick={this.markUnFinish}>×标记未完成</span>
            <span onClick={this.addReason}>*修改备注</span>
          </div>
          {/* 存放子分录 */}
          {this.state.activeBook && <div className="content">
            {this.state.activeBook.items.map((item, index) => {
              const itemClass = classnames({
                'content-item': true,
                'active-item': this.state.activeItem ? item.itemID === this.state.activeItem.itemID : false,
                'finish-status': item.status === 2,
                'unfinish-status': item.status === 1
              });
              return (
                <div key={index} className={itemClass} onClick={() => { this.selectItem(item); }}>
                  <div className="context">
                    <div className="main">
                      <div className="tips">第{index + 1}条、</div>
                      <div className="status">状态：{(item.status === 0 && '新增') || (item.status === 1 && '未完成') || (item.status === 2 && '完成')}</div>
                      <div className="context">内容：{item.content}</div>
                      <div className="time">创建时间：{this.showTime(item.createTime)}</div>
                      {item.status === 2 && <div className="time">完成时间：{this.showTime(item.finishTime)}</div>}
                    </div>
                    <div className="close" onClick={() => this.deleteItem(item)}>x</div>
                  </div>
                  {item.reason && <div className="reason">备注：{item.reason}</div>}
                </div>
              );
            })}
          </div>}
        </div>
      </div>
    </div>);
  }
}

export default Book;



